import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

/* Layout */
import Layout from '../views/layout/Layout'

export const constantRouterMap = [
  {path: '/login', component: () => import('@/views/login/index'), hidden: false},
  {path: '/question/card/edit', component: () => import('@/views/question/card/index'), hidden: true},
  {
    path: '/',
    component: Layout,
    redirect: '/home',
    meta: {title: '', icon: 'home'},
    children: [{
      path: 'home',
      name: 'home',
      component: () => import('@/views/home/index'),
      role: ['MANGER', 'TEACHER', 'STUDENT'],
      meta: {title: '', icon: 'home'}
    }]
  }
];


const router = new Router({
  mode: 'hash', //
  scrollBehavior: () => ({y: 0}),
  routes: constantRouterMap
});

export default router

//
//
export const asyncRouterMap = [

  {
    path: '/center',
    component: Layout,
    name: 'center',
    meta: {title: '', icon: 'product'},
    role: ['MANGER', 'TEACHER', 'STUDENT'],
    children: [
      {
        path: 'teacher',
        name: 'teacher',
        component: () => import('@/views/center/teacher'),
        meta: {title: '()', icon: 'home'},
        role: ['TEACHER', 'MANGER']
      },
      {
        path: 'student',
        name: 'student',
        component: () => import('@/views/center/student'),
        role: ['STUDENT'],
        meta: {title: '()', icon: 'home'}
      }
    ]
  },
  {
    path: '/school',
    component: Layout,
    name: 'student',
    meta: {title: '', icon: 'product'},
    role: ['MANGER'],
    children: [{
      path: 'faculty',
      name: 'faculty',
      role: ['MANGER'],
      component: () => import('@/views/student/faculty/index'),
      meta: {title: '', icon: 'product-list'}
    },
      {
        path: 'manager',
        name: 'manager',
        component: () => import('@/views/student/manager/index'),
        meta: {title: '', icon: 'product-list'},
        role: ['MANGER']
      },

      {
        path: 'profession',
        name: 'profession',
        component: () => import('@/views/student/profession/index'),
        meta: {title: '', icon: 'product-list'},
        role: ['MANGER']
      },
      {
        path: 'class',
        name: 'class',
        component: () => import('@/views/student/class/index'),
        meta: {title: '', icon: 'product-list'},
        role: ['MANGER']
      }, {
        path: 'manger',
        name: 'manger',
        component: () => import('@/views/student/teacher/index'),
        meta: {title: '', icon: 'product-list'},
        role: ['MANGER']
      },
    ]
  }, {
    path: "/task",
    name: "task",
    component: Layout,
    meta: {title: "", icon: 'example'},
    role: ['MANGER', 'TEACHER', 'STUDENT'],
    children: [
      {
        path: 'course',
        name: 'course',
        component: () => import('@/views/student/course/index'),
        meta: {title: '', icon: 'product-list'},
        role: ['MANGER']
      }, {
        path: 'teachTask',
        name: 'course',
        component: () => import('@/views/task/teachCourse/index'),
        meta: {title: '', icon: 'product-list'},
        role: ['TEACHER', 'MANGER', 'STUDENT']
      },
      {
        path: 'reply',
        name: 'reply',
        component: () => import('@/views/question/card/answer'),
        meta: {title: '', icon: 'product-list'},
        hidden: true,
        role: ['STUDENT']
      }, {
        path: 'view',
        name: 'view',
        component: () => import('@/views/question/card/view'),
        meta: {title: '', icon: 'product-list'},
        hidden: true,
        role: ['MANGER', 'TEACHER', 'STUDENT']
      }, {
        path: 'statisticsScore',
        name: 'statisticsScore',
        component: () => import('@/views/question/statistics/index'),
        meta: {title: '', icon: 'product-add'},
        role: ['MANGER', 'TEACHER'],
        hidden: true
      }, {
        path: 'rank',
        name: 'rank',
        component: () => import('@/views/question/rank/index'),
        meta: {title: '', icon: 'product-add'},
        role: ['MANGER', 'TEACHER'],
        hidden: true
      }
    ]
  },
  {
    path: "/rbac",
    name: "rbac",
    component: Layout,
    meta: {title: "", icon: 'example'},
    hidden: true,
    role: ['MANGER'],
    children: [{
      path: 'resource',
      name: 'resource',
      component: () => import('@/views/rbac/resource/resourceList'),
      meta: {title: '', icon: 'product-list'}
    }, {
      path: 'role',
      name: '/role',
      component: () => import('@/views/rbac/resource/roleList'),
      meta: {title: '', icon: 'product-list'}
    }
    ]
  },
  {
    path: '/questionnaire',
    component: Layout,
    name: 'questionnaire',
    meta: {title: '', icon: 'product'},
    role: ['MANGER'],
    children: [
      {
        path: 'create',
        name: 'create',
        component: () => import('@/views/question/card/create'),
        meta: {title: '', icon: 'product-list'},
        role: ['MANGER']
      }, {
        path: 'listView',
        name: 'listView',
        component: () => import('@/views/question/list/index'),
        meta: {title: '', icon: 'product-add'},
        role: ['MANGER']
      }
    ],
  },
  {path: '/404', component: () => import('@/views/404'), hidden: true},
  {path: '*', redirect: '/404', hidden: true},

];



