import {login, logout, getInfo} from '@/api/login'
import {getToken, setToken, removeToken} from '@/utils/auth'

const user = {
  state: {
    token: getToken(),
    name: '',
    avatar: '',
    roles: []
  },

  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_NAME: (state, name) => {
      state.name = name
    },
    SET_AVATAR: (state, avatar) => {
      state.avatar = avatar
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles
    }
  },

  actions: {
    // 
    Login({commit}, userInfo) {
      const username = userInfo.username.trim();
      return new Promise((resolve, reject) => {
        login(username, userInfo.password, userInfo.image).then(response => {
          const data = response.data;
          const tokenStr = data.authenticated;
          const principal = data.principal;
          const roles = principal.roleModels;
          const name = data.name;
          const image = principal.image;
          console.log("",data);
          console.log("token",tokenStr);
          console.log("",roles);
          console.log("",roles);
          console.log("",image);
          setToken(tokenStr);
          commit('SET_TOKEN', tokenStr);
          commit('SET_ROLES', roles);
          commit('SET_NAME', name);
          commit('SET_AVATAR', image);
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    // 
    GetInfo({commit, state}) {
      console.log("state",state);
      return new Promise((resolve, reject) => {
        getInfo().then(response => {
          console.log("",response);
          const data = response.data;
          const principal = data.principal;
          console.log("===principal",principal);
          console.log("===roleModels",principal.roleModels);
          commit('SET_NAME', data.name);
          commit('SET_AVATAR', principal.image);
          commit('SET_TOKEN', principal.enabled);
          commit('SET_ROLES', principal.roleModels);
          resolve(this.state.user)
        }).catch(error => {
          reject(error)
        })
      });
    },
    // 
    LogOut({commit, state}) {
      return new Promise((resolve, reject) => {
        logout(state.token).then(() => {
          commit('SET_TOKEN', '');
          commit('SET_ROLES', []);
          commit('SET_NAME', '');
          commit('SET_AVATAR', '');
          removeToken();
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    //  
    FedLogOut({commit}) {
      return new Promise(resolve => {
        commit('SET_TOKEN', '');
        removeToken();
        resolve();
      })
    }
  }
};

export default user



